import parseDataURL from "data-urls";

export const resizeImage = async (dataUrl: string, width: number, height: number, quality: number): Promise<string> => {
	const img = await createImageElement(dataUrl);
	const parsedUrl = parseDataURL(dataUrl);
	const mimeType = parsedUrl.mimeType.toString();

	if (img.width > width || img.height > height) {
		const canvas = document.createElement("canvas") as HTMLCanvasElement;
		const ratio = Math.min(width / img.width, height / img.height);
		const newWidth = img.width * ratio;
		const newHeight = img.height * ratio;
		canvas.width = newWidth;
		canvas.height = newHeight;
		canvas.getContext("2d").drawImage(img, 0, 0, newWidth, newHeight);
		return canvas.toDataURL(mimeType, quality);
	}

	return dataUrl;
};

const createImageElement = async (dataUrl: string): Promise<HTMLImageElement> => {
	return new Promise<HTMLImageElement>((resolve, reject) => {
		const image = new Image();
		image.onload = () => resolve(image);
		image.onerror = () => reject();
		image.src = dataUrl;
	});
};
